import api from '../../assets/js/api'
export default {
    components: {},
    data () {
        return {
            userInfo: {
                "user_id": "",
                "username": "",
                "nickname": "",
                "mobile": "",
                "avatar": "",
                "identity": "",
                "wx_name": ""
            },
            identity: {
                'store': '创客空间',
                'director': '创客主管',
                'member': '创客'
            },
            account: {
                balance: 0,
                frost: 0
            }
        }
    },
    methods: {
        layer(text){
            this.$vux.toast.text(text || 'hello', 'middle')
        },
        showLoading  (text){
            this.$vux.loading.show({
                text: text || '加载中'
            })
        },
        hideLoading(){
            this.$vux.loading.hide()
        },
        gotoPage(link) {
            this.$router.push({
                path: link
            })
        },
        submit() {
            if(this.account.balance <= 0){
                 this.layer('暂无余额可提现')
            }else{
                this.$router.push({
                    path: '/tixian'
                })
            }
        },
        getUserInfo(){
            this.$http.post(api.getUserInfo)
                .then(res => {
                    this.userInfo = res.data.userinfo;
                    this.account = res.data.account;
                })
        }
    },
    mounted() {
        this.getUserInfo();
        document.getElementsByTagName('title')[0].textContent = '账户余额';
    }
}



