export default {
    components: {},
    data () {
        return {}
    },
    methods: {
        layer(text){
            this.$vux.toast.text(text || 'hello', 'middle')
        },
        showLoading(){
            this.$vux.loading.show({
                text: '加载中'
            })
        },
        hideLoading(){
            this.$vux.loading.hide()
        },

    },
    mounted() {

    }
}



