
import InfiniteLoading from 'vue-infinite-loading';
import api from '../../assets/js/api'
import dateFormat from 'vux/src/tools/date/format.js'

import Nodata from '../../components/nodata.vue'
export default {
components: {
InfiniteLoading,
Nodata
},
filters: {
dateFormat
},
data () {
return {
page: 1,
frost: 0,
frostLog: [
]
}
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(text){
this.$vux.loading.show({
text: text || '加载中'
})
},
hideLoading(){
this.$vux.loading.hide()
},
getList ($state) {
this.$http.post(api.accountFrostLog)
.then(res => {
this.frost = res.data.frost
if (res.data.frostLog.length) {
this.page++
this.frostLog = this.frostLog.concat(res.data.frostLog)
$state.loaded()
} else {
$state.complete()
}
}).catch(e => {
$state.complete()
})
},
gotoSettleDetail(id){
this.$router.push({
path: `/settleDetail?id=${id}&type=rebate`
})
}
},
mounted() {
document.getElementsByTagName('title')[0].textContent = '待结算';
}
}



