
import InfiniteLoading from 'vue-infinite-loading';
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import api from '../../assets/js/api'
import Nodata from '../../components/nodata.vue'

export default {
components: {
Tab,
TabItem,
InfiniteLoading,
Nodata
},
data () {
return {
bottomCount: 0,
type: 0,
orders: [
{
/* 空间待处理 */
page: 1,
data: []
},
{
/* 历史订单 */
page: 1,
data: []
},
{
/* 总部待处理 */
page: 1,
data: []
}
]
}
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(){
this.$vux.loading.show({
text: '加载中'
})
},
hideLoading(){
this.$vux.loading.hide()
},
onItemClick(index){
this.type = index
this.$nextTick(() => {
this.$refs.infiniteLoading.$emit('$InfiniteLoading:reset');
})
},
getList ($state) {
let status = this.type == 0 ? 1 : this.type == 1 ? 3 : 4;
this.$http.post(api.delivery,{
page: this.orders[this.type].page,
status: status
}).then(res => {
if (res.data.length) {
this.orders[this.type].data = this.orders[this.type].data.concat(res.data);
this.orders[this.type].page++;
$state.loaded();
} else {
$state.complete();
}
}).catch(e => {
$state.complete();
})
},
/*订单操作删除，通过审核*/
passVerify(order_id,type){
this.showLoading('提交中')
this.$http.post(api.verify,{
ordersn: order_id,
type: type
}).then(res => {
this.hideLoading()
this.layer('审核通过')
/* todo:审核通过后的操作 */
}).catch(e => {
this.hideLoading()
})
},

changeOrderStatus(status,order_id){
this.showLoading('提交中')
this.$http.post(api.changeOrderStatus,{
ordersn: order_id,
status: status
}).then(res => {
this.hideLoading()
this.layer('修改成功')
}).catch(e => {
this.hideLoading()
})
},
gotoOrderitem(id){
this.$router.push({
path: `/fahuoDetail?order_id=${id}`
})
}
},
mounted() {

document.getElementsByTagName('title')[0].textContent = '发货管理';
}
}



