
import api from '../../assets/js/api'
import dateFormat from 'vux/src/tools/date/format.js'

import {getParams} from '../../assets/js/util'
export default {
components: {},
filters: {
dateFormat
},
data () {
return {
detail: {},
id: getParams()['id'] || '',
type: getParams()['type'] || ''
}
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(){
this.$vux.loading.show({
text: '加载中'
})
},
hideLoading(){
this.$vux.loading.hide()
},
gotoOrderDetail(){
this.$router.push({
path: `/orderDetail?order_id=${this.detail.ordersn}`
})
},
getDetail() {
this.showLoading('加载中')
this.$http.post(api.accountLogDetail, {
id: this.id,
type: this.type
}).then(res => {
this.hideLoading()
this.detail = res.data
}).catch(e => {
this.hideLoading()
})
},
},
mounted() {
this.getDetail()
document.getElementsByTagName('title')[0].textContent = '结算详情';
}
}



