
import InfiniteLoading from 'vue-infinite-loading';
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'

import api from '../../assets/js/api'
import Nodata from '../../components/nodata.vue'

export default {
components: {
Tab,
TabItem,
InfiniteLoading,
Nodata
},
data () {
return {
bottomCount: 0,
type: 0,
orders: [
{
/* 全部订单 */
page: 1,
data: []
},
{
/* 等待付款 */
page: 1,
data: []
},
{
/* 等待发货 */
page: 1,
data: []
},
{
/* 等待收货 */
page: 1,
data: []
}
]
}
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(){
this.$vux.loading.show({
text: '加载中'
})
},
hideLoading(){
this.$vux.loading.hide()
},
getList($state) {
this.$http.post(api.orderIndex,{
page: this.orders[this.type].page,
status: this.type
}).then(res => {
if (res.data.length) {
this.orders[this.type].data = this.orders[this.type].data.concat(res.data);
this.orders[this.type].page++;
$state.loaded();
} else {
$state.complete();
}
}).catch(e => {
$state.complete();
})
},
gotoOrderitem(id){
this.$router.push({
path: `/orderDetail?order_id=${id}`
})
},
onItemClick(index){
this.type = index
this.$nextTick(() => {
this.$refs.infiniteLoading.$emit('$InfiniteLoading:reset');
})
},

changeOrderStatus(status,order_id){
this.showLoading('提交中')
this.$http.post(api.changeOrderStatus,{
ordersn: order_id,
status: status
}).then(res => {
this.hideLoading()
this.layer('修改成功')
/* 更改订单状态成功 */
this.orders[this.type].data = [];
this.orders[this.type].page = 1;
this.$nextTick(() => {
this.$refs.infiniteLoading.$emit('$InfiniteLoading:reset');
})
}).catch(e => {
this.hideLoading()
})
},
/*跳转微信支付*/
gotoWeChatPay(order_id){
location.href = weChatPay(order_id)
},
/*跳转上传凭证页面*/
uploadVoucher(order_id,delivery){
this.$router.push({
path: `/offinePay?order_id=${order_id}&isPost=${delivery}`
})
}
},
mounted() {

document.getElementsByTagName('title')[0].textContent = '订单管理';
}
}



