
import XInput from 'vux/src/components/x-input/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

import api from '../../assets/js/api'
export default {
components: {
XInput
},
data () {
return {
oldPwd: '',
newPwd: '',
reNewPwd: ''
}
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(text){
this.$vux.loading.show({
text: text || '加载中'
})
},
hideLoading(){
this.$vux.loading.hide()
},
surePwd(){
if (!this.$refs.oldPwdInput.valid) {
this.layer('请输入原来的密码')
} else if (!this.$refs.newPwdInput.valid) {
this.layer('请输入新的密码')
} else if (this.newPwd != this.reNewPwd) {
this.layer('请输入一致的新密码')
} else {
this.showLoading('修改中')
this.$http.post(api.modifyPwd, {
oldpassword: this.oldPwd,
newpassword: this.newPwd,
renewpassword: this.reNewPwd
}).then(res => {
this.hideLoading();
this.layer('修改成功，重新登录')
cookie.remove('token')
this.$router.push({
path: '/login'
})
}).catch(e => {
this.hideLoading()
})
}
}
},
mounted() {
document.getElementsByTagName('title')[0].textContent = '修改密码';
}
}



