
import XInput from 'vux/src/components/x-input/index.vue'
import cookie from 'vux/src/tools/cookie/index.js'

import api from '../../assets/js/api'
import {getParams,weChatAuth} from '../../assets/js/util'

export default {
components: {
XInput
},
data () {
return {
userInfo: {
"user_id": "",
"username": "",
"nickname": "",
"mobile": "",
"avatar": "",
"identity": "",
"wx_name": ""
},
identity: {
'store': '创客空间',
'director': '创客主管',
'member': '创客'
},
account: {
balance: 1000,
frost: 1000
},
money: ''
}
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(){
this.$vux.loading.show({
text: '加载中'
})
},
hideLoading(){
this.$vux.loading.hide()
},
getUserInfo(){
this.$http.post(api.getUserInfo)
.then(res => {
this.userInfo = res.data.userinfo || {};
this.account = res.data.account || {};
/*先授权*/
if(!cookie.get('my_openId')){
location.href = weChatAuth()
}
})
},
submitCash(){
if (this.money <= 0 || parseFloat(this.money) > parseFloat(this.account.balance)) {
this.layer('请输入正确的提现金额')
} else {
this.showLoading('提现中')
this.$http.post(api.getCash, {
money: this.money
}).then(res => {
this.hideLoading();
this.$router.push({
path: '/tixiansuc'
})
}).catch(e => {
this.hideLoading();
})
}
}
},
mounted() {
this.getUserInfo();
document.getElementsByTagName('title')[0].textContent = '提现';
}
}



