
import InfiniteLoading from 'vue-infinite-loading';
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import dateFormat from 'vux/src/tools/date/format.js'

import api from '../../assets/js/api'
import Nodata from '../../components/nodata.vue'

export default {
components: {
Tab,
TabItem,
InfiniteLoading,
Nodata
},
filters: {
dateFormat
},
data () {
return {
bottomCount: 0,
type: 0,
markers: [
{
/* 待审核 */
page: 1,
data: []
},
{
/* 已代理 */
page: 1,
data: []
},
{
/* 已拒绝 */
page: 1,
data: []
}
]
}
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(){
this.$vux.loading.show({
text: '加载中'
})
},
hideLoading(){
this.$vux.loading.hide()
},
onItemClick(index){
this.type = index
this.$nextTick(() => {
this.$refs.infiniteLoading.$emit('$InfiniteLoading:reset');
})
},
getStatus(status){
return status == 0 ? '审核中' :  status == 1 ? '已通过' : status == 2 ? '已拒绝' : '已取消资格';
},
getList($state){
let type = this.type == 0 ? 'verify' : this.type == 1 ? 'agent' : 'refuse';
this.$http.post(api.makerManage,{
page: this.markers[this.type].page,
type: type
}).then(res => {
if (res.data.length) {
this.markers[this.type].data = this.markers[this.type].data.concat(res.data);
this.markers[this.type].page++;
$state.loaded();
} else {
$state.complete();
}
}).catch(e => {
$state.complete();
})
},
showRefuseConfirm(id){
const _this = this
this.$vux.confirm.show({

showInput: true,
title: '请填写拒绝原因,反馈给申请人',
placeholder: '限15字以内',
inputAttrs: {
type: 'textarea'
},
onCancel () {
console.log(this) 
},
onConfirm (msg) {
_this.makerVerifyApply(id,'refuse',msg)
}
})
},
cancelConfirm(id){
const _this = this
this.$vux.confirm.show({

showInput: true,
title: '是否确认取消其代理资格？',
placeholder: '（选填）可在此填写取消资格原因',
inputAttrs: {
type: 'textarea'
},
onCancel () {
console.log(this) 
console.log(_this) 
},
onConfirm (msg) {
_this.makerVerifyApply(id,'cancel',msg)
}
})
},
makerVerifyApply(id,type,remark){
this.showLoading('提交中')
this.$http.post(api.makerVerifyApply,{
id: id,
type: type,
remark: remark
}).then(res => {
this.hideLoading()
this.layer('操作成功')
this.markers[this.type].data = []
this.markers[this.type].page = 1
this.$nextTick(() => {
this.$refs.infiniteLoading.$emit('$InfiniteLoading:reset');
})
}).catch(e => {
this.hideLoading()
})
}
},
mounted() {

}
}



