
import XInput from 'vux/src/components/x-input/index.vue'
import Countdown from 'vux/src/components/countdown/index.vue'
import Previewer from 'vux/src/components/previewer/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'

import api from '../../assets/js/api'
import UploadPhoto from '../../components/uploadPhoto.vue'
import {getParams} from '../../assets/js/util'
export default {
directives: {
TransferDom
},
components: {
XInput,
Countdown,
UploadPhoto,
Previewer
},
data () {
return {
name: '',
phone: '',
code: '',
password: '',
rePassword: '',
time: 90,
start: true,
isSendCoding: false,
list: [











],
id: getParams()['id'] || '',
tjname: getParams()['name'] || '',
}
},
methods: {
layer(text){
this.$vux.toast.text(text || 'hello', 'middle')
},
showLoading(){
this.$vux.loading.show({
text: '加载中'
})
},
hideLoading(){
this.$vux.loading.hide()
},
finish(){
this.isSendCoding = false;
this.time = 90;
},
sendCode(){
if (!this.$refs.phoneInput.valid) {
this.layer('请输入正确的手机号码')
} else {
this.$http.post(api.getCode, {
event: 'register',
mobile: this.phone
}).then(res => {
this.isSendCoding = true;
})
}
},
chooseImg(){
/* 解决fastClick的延迟的bug */
this.$refs['uploadPhoto'].$el.click();
},

getImgDataUrl(value){

},
uploadProgress(value){


},
uploadResData(value){
let _this = this
if (value.type == 'success') {
let img = new Image();
let that = this;
let imgUrl = 'http://qjwx.dianduode.com' +  value.url;
img.src = imgUrl

img.onload = function () {

_this.list.push({
msrc: imgUrl,
src: imgUrl
})
}
} else {

}
},
show (index) {
this.$refs.previewer.show(index)
},
deletePhoto (index){
this.list.splice(index, 1);
},
submit(){
if (!this.$refs.nameInput.valid) {
this.layer('请填写真实姓名')
} else if (!this.$refs.phoneInput.valid) {
this.layer('请填写正确的手机号')
} else if (!this.$refs.codeInput.valid) {
this.layer('请填写验证码')
} else if (!this.$refs.pwdInput.valid) {
this.layer('请填写密码')
} else if (!this.$refs.rePwdInput.valid) {
this.layer('请确认密码')
} else if (this.list.length <= 0) {
this.layer('请上传合同')
} else {
let imgs = []
this.list.forEach( val => {
imgs.push(val.src)
} )
this.showLoading('提交中')
this.$http.post(api.registerMaker,{
makerId: this.id,
realname: this.name,
phone: this.phone,
password: this.password,
contract: JSON.stringify(imgs)
}).then(res => {
this.hideLoading()
this.layer('注册完成请登录')
this.$router.push({
path: '/'
})
}).catch(e =>{
this.hideLoading()
})
}
}
},
mounted() {

document.getElementsByTagName('title')[0].textContent = '注册';
}
}



